/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { Form, Button } from 'react-bootstrap';
import { useState, useCallback, ChangeEvent, Dispatch, SetStateAction, FormEvent } from 'react';
import { LoginScheme } from 'models/auth';
import Loading from 'components/Utils/Loading';
import { Color } from 'src/constants';

interface Props {
  onSubmit: (data: LoginScheme) => void;
  loading: boolean;
}
const EmailPasswordForm = ({ onSubmit, loading = false }: Props) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const handleInputChange = useCallback((handler: Dispatch<SetStateAction<string>>) => {
    return (e: ChangeEvent<HTMLInputElement>) => handler(e.target.value);
  }, []);

  const handleSubmit = useCallback(
    async (e: FormEvent<HTMLFormElement>) => {
      e.preventDefault();
      onSubmit({
        email,
        password,
      });
    },
    [email, onSubmit, password]
  );

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Control
        size="lg"
        type="email"
        defaultValue={email}
        onChange={handleInputChange(setEmail)}
        placeholder=""
        css={css`
          margin-bottom: 1rem;
        `}
        disabled={loading}
      />
      <Form.Control
        size="lg"
        type="password"
        defaultValue={password}
        onChange={handleInputChange(setPassword)}
        placeholder=""
        css={css`
          margin-bottom: 1rem;
        `}
        disabled={loading}
      />
      <Button
        css={css`
          width: 100%;
        `}
        type="submit"
        disabled={loading}
        size="lg"
      >
        {loading ? <Loading size={40} color={Color.White} /> : ''}
      </Button>
    </Form>
  );
};

export default EmailPasswordForm;
