/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { LoginScheme } from 'models/auth';
import { useCallback, useState, Fragment, useEffect } from 'react';
import LubyconMemberForm from 'components/Forms/Login/LubyconMemberForm';
import { Alert } from 'react-bootstrap';
import { useAuth } from 'hooks/useAuth';

const LoginForm = () => {
  const { loginByEmail } = useAuth();
  const [errorMessage, setErrorMessage] = useState('');
  const [isLoading, setLoadingFlag] = useState(false);

  const loginAsLubyconMember = useCallback(
    async ({ email, password }: LoginScheme) => {
      setLoadingFlag(true);
      try {
        await loginByEmail(email, password);
      } catch (e) {
        setErrorMessage(e.message);
      } finally {
        setLoadingFlag(false);
      }
    },
    [loginByEmail, setLoadingFlag]
  );

  useEffect(() => {
    if (errorMessage.length > 0) {
      setTimeout(() => {
        setErrorMessage('');
      }, 3000);
    }
  }, [errorMessage]);

  return (
    <Fragment>
      <LubyconMemberForm onSubmit={loginAsLubyconMember} loading={isLoading} />
      <Alert
        css={css`
          margin: 2rem 0 0;
        `}
        show={errorMessage.length > 0}
        variant="danger"
      >
        {errorMessage}
      </Alert>
    </Fragment>
  );
};

export default LoginForm;
